<template>
  <!-- 机构管理 -->
  <el-container class="permission-assignment">
    <el-aside width='20%' class="permission-assignment-aside">
      <el-input placeholder="机构查询" v-model="filterText" size="small" :style="{'margin-bottom':'10px'}"></el-input>
      <!--<el-scrollbar :style="{'height':siteContentViewHeight - 58 + 'px' }">-->
        <div :style="{'height':siteContentViewHeight - 52 + 'px' ,overflow:'auto'}">
          <org-tree ref="tree" :treeData="treeData"
                    :filterText='filterText'
                    @node-click="orgTreeClick"></org-tree>
        </div>
      <!--</el-scrollbar>-->
    </el-aside>
    <el-main>
      <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
        <el-tab-pane label="机构信息" name="first">
          <organization-infor ref='organizationInfor' @refreshDataList="getDataTree(1)"></organization-infor>
        </el-tab-pane>
        <el-tab-pane label="编制信息" name="second">
          <compiling-infor ref='compilingInfor'></compiling-infor>
        </el-tab-pane>
      </el-tabs>
    </el-main>
  </el-container>
</template>

<script>
  import orgTree from "@/components/baseComponents/orgTree.vue";
  import organizationInfor from './organization-information.vue'
  import compilingInfor from './compiling-information.vue'
  export default{
    props:['siteContentViewHeight'],
    components:{
      orgTree,organizationInfor,compilingInfor
    },
    data(){
      return{
        treeData:[],
        filterText:'',
        activeName:'first',
        activeOrg:{}
      }
    },
    methods:{
      getDataTree(key){//获取机构
        this.$http({
          url: this.$http.adornUrl(this.API.getOrgTreeB01),
          method: 'post',
          data: this.$http.adornData()
        }).then(({data}) => {
          if(data && data.code===0){
            if(data.data.length>0){
              this.treeData=data.data;
              if(key==undefined) this.orgTreeClick(this.treeData[0])
            }
          }else{
            this.$message.error(data.msg);
            this.treeData=[];
          }
        });
      },
      orgTreeClick(data) {
        this.isActiveInit(this.activeName,data)
        this.activeOrg=data;
      },
      handleClick(tab){
        this.isActiveInit(tab.name,this.activeOrg)
      },
      isActiveInit(name,data){
        this.$nextTick(() => {
          if(name=='first') this.$refs.organizationInfor.init(data);
          else if(name=='second') this.$refs.compilingInfor.init(data);
        })
      }
    },
    created() {
      this.getDataTree();
    }
  }
</script>
<style lang="scss" scoped>
  .permission-assignment{
    &-aside{
      padding: 10px 10px 0;
      border-right: 1px solid #e6e6e6;
      .el-scrollbar{padding: 8px 0;}
    }
    .el-tabs{
      padding-left: 10px;
      position: relative;
      /deep/ .el-tabs__header::before{
        content:" ";
        width: 10px;
        height: 1px;
        position: absolute;
        left: -10px;
        top: 40px;
        background: #e4e7ed;
      }
    }
  }
</style>



