<template>
  <!-- 编制信息 -->
  <div class="organization-information">
    <div class="organization-information-title" v-if="orgData.id">
      <span></span>
      <p>{{orgData.name}}</p>
    </div>
    <el-form class="float-right" :inline="true">
      <el-form-item v-if="isNoEdit">
        <el-button type="primary" size="small" @click="isNoEdit=false">编辑</el-button>
        <el-button type="primary" size="small" @click="deleteHandle">删除</el-button>
      </el-form-item>
      <el-form-item v-else>
        <el-button type="primary" size="small" @click="saveHandle">保存</el-button>
        <!-- <el-button type="primary" size="small" @click="isNoEdit=true">取消</el-button> -->
      </el-form-item>
    </el-form>
    <div class="clearfix"></div>
    <el-form class="organization-information-form" label-width="200px" label-position="left" :disabled='isNoEdit'>
      <el-row>
        <el-col :span="12">
          <el-form-item label="编制批准日期" prop="b0201">
            <el-date-picker v-model="form.b0201" type="date" format="yyyy.MM.dd" value-format="yyyy-MM-dd"></el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="编制批准文号" prop="b0204">
            <el-input v-model="form.b0204"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="编制批准机关名称" prop="b0207A">
            <el-input v-model="form.b0207A"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="编制批准机关代码" prop="b0207B">
            <el-input v-model="form.b0207B"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="内设机构数" prop="b0211">
            <el-input v-model="form.b0211"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="内设机构名称" prop="b0214">
            <el-input v-model="form.b0214"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="内设机构级别" prop="b0217">
            <el-input v-model="form.b0217"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="编制性质" prop="b0221">
            <el-input v-model="form.b0221"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="批准编制总数" prop="b0224">
            <el-input v-model="form.b0224"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="行政编制数" prop="b0227">
            <el-input v-model="form.b0227"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="事业编制数" prop="b0231">
            <el-input v-model="form.b0231"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="企业编制数" prop="b0234">
            <el-input v-model="form.b0234"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="社团编制数" prop="b0237">
            <el-input v-model="form.b0237"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="单位领导职数" prop="b0241">
            <el-input v-model="form.b0241"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="单位领导同级非职务数" prop="b0244">
            <el-input v-model="form.b0244"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="内设第一级机构领导职数" prop="b0247">
            <el-input v-model="form.b0247"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="内设第一级机构领导非职数" prop="b0251">
            <el-input v-model="form.b0251"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="业务人员编制数" prop="b0254">
            <el-input v-model="form.b0254"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="工勤人员编制数" prop="b0257">
            <el-input v-model="form.b0257"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="使用全额拨款人数" prop="b0261">
            <el-input v-model="form.b0261"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="使用差额拨款人数" prop="b0264">
            <el-input v-model="form.b0264"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="使用自筹资金人数" prop="b0267">
            <el-input v-model="form.b0267"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>
<script>
  export default{
    data(){
      return{
        isNoEdit:true,
        form:this.newData(),
        orgData:{id:'',name:'',b0200:''}
      }
    },
    methods:{
      newData(){
      	return {b0201:'',b0204:'',b0207A:'',b0207B:'',b0211:'',b0214:'',b0217:'',b0221:'',b0224:'',b0227:'',b0231:'',b0234:'',b0237:'',b0241:'',b0244:'',b0247:'',b0251:'',b0254:'',b0257:'',b0261:'',b0264:'',b0267:''};
      },
      init(org){
        this.orgData.id=org.id;
        this.orgData.name=org.name;
        this.isNoEdit=true;
        this.$http({
          url: this.$http.adornUrl(this.API.queryOrganizationB02),
          method: 'post',
          data: this.$http.adornData({
            b0100:org.id
          })
        }).then(({data}) => {
          this.form=this.newData();
          this.orgData.b0200='';
          if(data && data.code===0){
            if(data.data!=null){
              this.form=data.data;
              this.orgData.b0200=this.form.b0200;
            }
          }else{
            this.$message.error(data.msg);
          }
        });
      },
      saveHandle(){
        this.form['b0100']=this.orgData.id;
        this.form['b0200']=this.orgData.b0200;
        this.$http({
          url: this.$http.adornUrl(this.API.updateOrganizationB02),
          method: 'post',
          data: this.$http.adornData(this.form)
        }).then(({data}) => {
          if(data && data.code===0){
            this.$message.success('操作成功');
          }else{
            this.$message.error(data.msg);
          }
        });
      },
      deleteHandle(){
        this.$confirm('确定删除机构,是否继续？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          if(this.orgData.b0200!=''){
            this.form=this.newData();
            this.saveHandle();
          }else this.$message.error('删除失败!没有数据可删除');
        }).catch(() => {})
      }
    }
  }
</script>
<style lang="scss" scoped>
  .organization-information{
    &-title{
      float: left;
      padding-left: 10px;
      color: #f0992b;
      margin-top: 7px;
      span{
        display: inline-block;
        width:10px;
        height: 10px;
        background: rgba(#f0992b,0.6);
        box-shadow: 0px 0px 6px 0px #f0992b;border-radius: 50%;}
      p{display: inline-block; padding-left: 10px;}
    }
    .el-form--inline .el-form-item{margin-bottom: 10px;margin-top: 10px;}
    &-form{
      padding: 0 6px 0 0;
      .el-form-item{
        border:1px solid #D1CECE;
        margin: 0 4px 10px;
        /deep/ .el-form-item__label{padding-left: 6px;border-right: 1px solid #D1CECE;background: #F9F9F9;}
        /deep/ .el-input__inner{border: none}
        /deep/ .el-radio-group{padding-left: 10px;}
      }
      /deep/ .el-input.is-disabled .el-input__inner{background: none;}
      /deep/ .el-date-editor.el-input, .el-date-editor.el-input__inner{width: 100%;
        .el-input__prefix{left: inherit;right: 5px;}
      }
      /deep/ .el-input--prefix .el-input__inner{padding-left: 15px;}
    }
  }
</style>



